export class CronaVireus {
    country: string = '';
    countryInfo: subcountryInfo = new subcountryInfo;
    cases: number = 0;
    todayCases: number = 0;
    deaths: number = 0;
    todayDeaths: number = 0;
    recovered: number = 0;
    active: number = 0;
    critical: number = 0;
    casesPerOneMillion: number = 0;
    deathsPerOneMillion: number = 0;
}
export class subcountryInfo {
    _id: number = 0;
    country: string = '';
    iso2: string = '';
    iso3: string = '';
    lat: number = 0;
    long: number = 0;
    flag: string = '';
}
