import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CronaVireus } from './cronaData.model';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  _Cronaurl = "https://corona.lmao.ninja/countries";
  _CronaurlWorled = "https://corona.lmao.ninja/all";


  constructor(private http: HttpClient) { }
  
   // For Get Data About CronaVireus
  getCronaData()  {
    return this.http.get<CronaVireus[]>(this._Cronaurl);
  }
    // For Get Data About CronaVireus
    getCronaDataToWholeWorled()  {
      return this.http.get<CronaVireus[]>(this._CronaurlWorled);
    }
}
